import Swiper, { Navigation, Pagination, Autoplay, EffectFade, Parallax} from 'swiper';

const delay = 1500;
const bulletBarLine = document.querySelector('.slider-pagination-bar__line')
const swiper = new Swiper('.swiper', {
	modules: [Navigation, Pagination, Autoplay, EffectFade, Parallax],
	rebuildOnUpdate: true,
	observer: true,
    observeParents: true,
  	loop: true,
	slidersPerView: 1,
	speed: delay,
	parallax: true,
	fadeEffect: {
		crossFade: true
	},
	effect: 'fade',
	
	autoplay: {
		delay: 2000,
		disableOnInteraction: false,
	},

	navigation: {
		nextEl: '.slider-button-next',
		prevEl: '.slider-button-prev',
	},
	breakpoints: {
		320: {
			pagination: {
				el: '.slider-pagination',
				type: 'bullets'
			},
		},
		992: {
			pagination: {
				el: '.slider-pagination',
				type: 'fraction',
				formatFractionCurrent: addZero,
        		formatFractionTotal: addZero
			},
		}
	},
	
});
function addZero(num){
	return (num > 9) ? num : '0' + num;
  }
  bulletBarLine.style=`width:100%;transition: ${delay}ms;`

swiper.on('slideChange', function () {
	bulletBarLine.style=`width:0`
	setTimeout(()=>{
		bulletBarLine.style=`width:100%;transition: ${delay}ms;`
	}, 0)
});
window.addEventListener("resize", function() {
	swiper.update()
    
});


